@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&display=swap");
:root {
  --color-primary: #e62a15;
  --color-grad-1: #f5af19;
  --color-grad-2: #f12711;
  --color-primary-variant: #f12711;
  --color-wrapper: #ff922b;
  --color-background: #fff;
  --color-grey--1: #f9f5f3;
  --color-grey--2: #f2efee;
  --color-text--1: #615551;
  --color-text--2: #918581;
  --color-text--3: #d3c7c3;
  --color-on-primary: #fff;
  --color-input-borders: #ddd;
  --color-grey--3: #e7e5e4;
  --gradient: linear-gradient(
    to right bottom,
    var(--color-grad-1),
    var(--color-grad-2)
  );
  --wrapper-gradient: linear-gradient(
    to right bottom,
    var(--color-wrapper),
    var(--color-grad-2)
  );
}

[data-theme="dark"] {
  --color-primary: #ffc078;
  --color-grad-1: #303030;
  --color-grad-2: #303030;
  --color-primary-variant: #ff922b;
  --color-wrapper: #121212;
  --color-background: #303030;
  --color-grey--1: #424242;
  --color-grey--2: #383838;
  --color-text--1: #ececec;
  --color-text--2: #bec5c7;
  --color-text--3: #989898;
  --color-on-primary: #000;
  --color-input-borders: #121212;
  --color-grey--3: #e7e5e4;
  --gradient: linear-gradient(
    to right bottom,
    var(--color-primary-variant),
    var(--color-primary-variant)
  );
  --wrapper-gradient: linear-gradient(
    to right bottom,
    var(--color-wrapper),
    var(--color-wrapper)
  );
}

* {
  margin: 0;
  padding: 0;
  transition: background-color 0.3s;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

@media only screen and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (max-width: 41.25em) {
  html {
    font-size: 43.75%;
  }
}

@media only screen and (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

body {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 400;
  line-height: 1.6;
  color: var(--color-text--1);
  background-image: var(--wrapper-gradient);
  background-size: cover;
  background-repeat: no-repeat;
  min-height: calc(100vh - 2 * 4vw);
}

.container-home {
  max-width: 120rem;
  margin: 4vw auto;
  min-height: 115rem;
  background-color: var(--color-background);
  border-radius: 9px;
  overflow: hidden;
  box-shadow: 0 2rem 6rem 0.5rem rgba(0, 0, 0, 0.2);
  display: grid;
  grid-template-rows: 10rem minmax(100rem, auto);
  grid-template-columns: 1fr 2fr;
  grid-template-areas: "head head" "list recipe";
}

@media only screen and (max-width: 75em) {
  .container-home {
    max-width: 100%;
    margin: 0;
    border-radius: 0;
  }
}

@media only screen and (max-width: 41.25em) {
  .container-home {
    grid-template-rows: 10rem auto;
    grid-template-columns: 1fr;
    grid-template-areas: "head" "list" "recipe";
  }
}

.container-pages {
  max-width: 120rem;
  margin: 4vw auto;
  min-height: 100rem;
  background-color: var(--color-background);
  border-radius: 9px;
  overflow: hidden;
  box-shadow: 0 2rem 6rem 0.5rem rgba(0, 0, 0, 0.2);
  display: grid;
  grid-template-rows: 10rem minmax(100rem, auto);
  grid-template-columns: 1fr;
  grid-template-areas: "head" "main";
}

@media only screen and (max-width: 75em) {
  .container-pages {
    max-width: 100%;
    margin: 0;
    border-radius: 0;
  }
}

@media only screen and (max-width: 41.25em) {
  .container-pages {
    grid-template-rows: 10rem minmax(130rem, auto);
  }
  .container-pages__planner {
    grid-template-rows: 10rem auto;
  }
}

.section {
  padding: 5rem 8rem 0rem 8rem;
  margin-bottom: 6rem;
  text-align: center;
}

.section__title {
  margin-bottom: 2rem;
}

.section__title::after,
.section__title::before {
  content: "";
  display: inline-block;
  width: 35%;
  height: 1px;
  background-color: var(--color-text--3);
  transform: translateY(1.5rem);
}

.section__title::before {
  float: left;
}

.section__title::after {
  float: right;
}

@media only screen and (max-width: 56.25em) {
  .section__title::after,
  .section__title::before {
    width: 0%;
  }
}

@media only screen and (max-width: 41.25em) {
  .section__shopping-list {
    margin-top: 3rem;
  }
}

.section__list {
  display: grid;
  justify-items: center;
}

.section__planner {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: space-between;
}

@media only screen and (max-width: 75em) {
  .section__planner {
    padding: 5rem 5rem 0rem 5rem;
  }
}

@media only screen and (max-width: 41.25em) {
  .section__planner {
    padding: 0;
    margin-top: 8rem;
    justify-content: flex-start;
    height: -moz-fit-content;
    height: fit-content;
  }
}

@media only screen and (max-width: 41.25em) {
  .section__recipe {
    padding: 0;
  }
}

.section__features {
  padding: 0;
}

.btn,
.btn--small,
.btn--small:link,
.btn--small:visited {
  background-image: var(--gradient);
  border-radius: 10rem;
  border: none;
  text-transform: uppercase;
  color: var(--color-on-primary);
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: all 0.2s;
}

.btn:hover,
.btn--small:hover {
  transform: scale(1.05);
}

.btn:focus,
.btn--small:focus {
  outline: none;
}

.btn > *:first-child,
.btn--small > *:first-child {
  margin-right: 1rem;
}

.btn {
  padding: 1.5rem 4rem;
  font-size: 1.5rem;
  font-weight: 700;
}

.btn svg {
  height: 2.25rem;
  width: 2.25rem;
  fill: currentColor;
}

.btn--small,
.btn--small:link,
.btn--small:visited {
  font-size: 1.4rem;
  font-weight: 700;
  padding: 1.25rem 2.25rem;
  text-decoration: none;
}

.btn--small svg,
.btn--small:link svg,
.btn--small:visited svg {
  height: 1.75rem;
  width: 1.75rem;
  fill: currentColor;
}

.btn--inline {
  color: var(--color-primary);
  font-size: 1.3rem;
  font-weight: 600;
  border: none;
  background-color: var(--color-grey--1);
  padding: 0.8rem 1.2rem;
  border-radius: 10rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: all 0.2s;
}

.btn--inline svg {
  height: 1.6rem;
  width: 1.6rem;
  fill: currentColor;
  margin: 0 0.2rem;
}

.btn--inline span {
  margin: 0 0.4rem;
}

.btn--inline:hover {
  color: var(--color-primary-variant);
  background-color: var(--color-grey--2);
}

.btn--inline:focus {
  outline: none;
}

.btn--week {
  font-size: 1.3rem;
  font-weight: 600;
  border: none;
  background-color: var(--color-grey--1);
  color: var(--color-text--1);
  padding: 1.2rem 1.6rem;
  border-radius: 10rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: all 0.2s;
}

.btn--week svg {
  height: 1.6rem;
  width: 1.6rem;
  fill: currentColor;
  margin: 0 0.2rem;
}

.btn--week span {
  text-transform: uppercase;
}

.btn--week:hover {
  color: var(--color-primary-variant);
  background-color: var(--color-grey--2);
}

.btn--week:focus {
  outline: none;
}

.btn--round {
  background-image: var(--gradient);
  border-radius: 50%;
  border: none;
  cursor: pointer;
  height: 4.5rem;
  width: 4.5rem;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn--round:hover {
  transform: scale(1.07);
}

.btn--round:focus {
  outline: none;
}

.btn--round svg {
  height: 2.5rem;
  width: 2.5rem;
  fill: var(--color-on-primary);
}

.btn--tiny {
  height: 2rem;
  width: 2rem;
  border: none;
  background: none;
  cursor: pointer;
}

.btn--tiny svg {
  height: 100%;
  width: 100%;
  fill: var(--color-primary);
  transition: all 0.3s;
}

.btn--tiny:focus {
  outline: none;
}

.btn--tiny:hover svg {
  fill: var(--color-primary-variant);
  transform: translateY(-1px);
}

.btn--tiny:active svg {
  fill: var(--color-primary-variant);
  transform: translateY(0);
}

.btn--tiny:not(:last-child) {
  margin-right: 0.3rem;
}

.btn--white {
  align-self: center;
  width: -moz-fit-content;
  width: fit-content;
  background-image: none;
  background-color: var(--color-grey--2);
  color: var(--color-primary);
}

.btn--white:not(:last-child) {
  margin-bottom: 2rem;
}

.btn--list {
  color: var(--color-primary);
  font-size: 2rem;
  font-weight: 600;
  border: none;
  background-color: var(--color-grey--2);
  padding: 0.8rem 1rem;
  border-radius: 1rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: all 0.2s;
}

.btn--list:not(:last-child) {
  margin-right: 1rem;
}

.btn--list svg {
  height: 2.4rem;
  width: 2.4rem;
  fill: currentColor;
  margin: 0 0.2rem;
  transform: translateX(2px);
}

.btn--list .list__icon--add {
  transform: translate(4px, 4px);
}

.btn--list span {
  margin: 0 3rem;
}

.btn--list:hover {
  color: var(--color-primary-variant);
  filter: brightness(0.95);
}

.btn--list:focus {
  outline: none;
}

.heading--2 {
  font-size: 2rem;
  font-weight: 700;
  color: var(--color-primary);
  text-transform: uppercase;
  margin-bottom: 2.5rem;
  text-align: center;
}

.heading--3 {
  font-size: 1.6rem;
  font-weight: 700;
  color: var(--color-text--1);
  text-transform: uppercase;
  margin: 1.5rem 0;
  text-align: center;
}

.link:link,
.link:visited {
  color: var(--color-text--2);
}

.spinner {
  margin: 5rem auto;
  text-align: center;
}

.spinner svg {
  height: 6rem;
  width: 6rem;
  fill: var(--color-primary);
  animation: rotate 2s infinite linear;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

.message,
.error {
  max-width: 40rem;
  margin: 0 auto;
  padding: 5rem 4rem;
  display: flex;
}

.message svg,
.error svg {
  height: 3rem;
  width: 3rem;
  fill: var(--color-primary);
  transform: translateY(-0.3rem);
}

.message p,
.error p {
  margin-left: 1.5rem;
  font-size: 1.8rem;
  line-height: 1.5;
  font-weight: 600;
  color: var(--color-text--1);
}

.message a:link,
.message a:visited,
.error a:link,
.error a:visited {
  text-decoration: none;
  color: var(--color-primary);
}

.message a:hover,
.error a:hover {
  text-decoration: underline;
}

.header {
  grid-area: head;
  background-color: var(--color-grey--1);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  margin-left: 2.5rem;
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 56.25em) {
  .header__logo {
    margin-left: 2.5rem;
    margin-right: 1.5rem;
  }
}

@media only screen and (max-width: 41.25em) {
  .header__logo {
    margin-left: 2.5rem;
    margin-right: 1rem;
  }
}

.header__logo-img {
  height: 5rem;
  margin-right: 1rem;
}

@media only screen and (max-width: 75em) {
  .header__logo-img--home {
    margin-right: 0;
  }
}

.header__logo-name {
  font-family: "Akaya Kanadaka", serif;
  font-size: 3.4rem;
  transform: translateY(-3px);
}

@media only screen and (max-width: 75em) {
  .header__logo-name--home {
    display: none;
  }
}

.search {
  background-color: var(--color-grey--2);
  border-radius: 10rem;
  display: flex;
  align-items: center;
  padding-left: 3rem;
  margin-right: 1.5rem;
  transition: all 0.3s;
}

.search:focus-within {
  transform: translateY(-2px);
}

.search__field {
  border: none;
  background: none;
  font-family: inherit;
  color: inherit;
  font-size: 1.7rem;
  width: 25rem;
}

.search__field:focus {
  outline: none;
}

.search__field::-moz-placeholder {
  color: var(--color-text--3);
}

.search__field::placeholder {
  color: var(--color-text--3);
}

@media only screen and (max-width: 56.25em) {
  .search__field {
    width: auto;
    max-width: 14rem;
  }
  .search__field::-moz-placeholder {
    color: var(--color-grey--2);
  }
  .search__field::placeholder {
    color: var(--color-grey--2);
  }
}

.search__btn {
  font-weight: 700;
  font-family: inherit;
  padding: 1.5rem 3rem;
}

@media only screen and (max-width: 75em) {
  .search__btn {
    padding: 1.5rem 2.5rem;
  }
  .search__btn span {
    display: none;
  }
  .search__btn svg {
    margin-right: 0 !important;
  }
}

@media only screen and (max-width: 41.25em) {
  .search__btn {
    padding: 1.5rem 3rem;
  }
  .search__btn span {
    display: initial;
  }
  .search__btn svg {
    margin-right: 1rem !important;
  }
}

@media only screen and (max-width: 28.12em) {
  .search__btn {
    padding: 1.5rem 2.5rem;
  }
  .search__btn span {
    display: none;
  }
  .search__btn svg {
    margin-right: 0 !important;
  }
}

.search__icon--header {
  margin-right: 0;
}

.page-options {
  align-self: stretch;
}

@media only screen and (max-width: 41.25em) {
  .page-options {
    background-color: var(--color-grey--1);
    padding: 1rem 0.5rem;
    border-radius: 3rem;
    position: absolute;
    z-index: 100;
    top: 11rem;
    right: 2rem;
  }
}

.page-options__list {
  list-style: none;
  display: flex;
  height: 100%;
}

.page-options__item {
  position: relative;
}

.page-options__btn {
  height: 100%;
  font-family: inherit;
  color: inherit;
  font-size: 1.4rem;
  font-weight: 700;
  text-transform: uppercase;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0 1rem;
  transition: all 0.3s;
  display: flex;
  align-items: center;
}

.page-options__btn--home {
  text-decoration: none;
}

.page-options__btn svg {
  height: 2.4rem;
  width: 2.4rem;
  fill: var(--color-primary);
  margin-right: 0.7rem;
  transform: translateY(-1px);
}

.page-options__btn:focus {
  outline: none;
}

.page-options__btn:hover {
  background-color: var(--color-grey--2);
}

.page-options-end {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  margin-right: 0.5rem;
}

@media only screen and (max-width: 41.25em) {
  .page-options-end {
    display: block;
    width: -moz-fit-content;
    width: fit-content;
    margin-right: 0;
  }
}

.bookmarks {
  padding: 1rem 0;
  position: absolute;
  right: -2.5rem;
  z-index: 10;
  width: 40rem;
  background-color: var(--color-grey--1);
  box-shadow: 1rem 1rem 4rem rgba(221, 221, 221, 0.1);
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s 0.2s;
}

@media only screen and (max-width: 41.25em) {
  .bookmarks {
    right: 0;
  }
}

.bookmarks__list {
  list-style: none;
}

.bookmarks__field {
  cursor: pointer;
  padding: 0 4rem;
  display: flex;
  align-items: center;
  height: 100%;
  transition: all 0.3s;
}

.bookmarks__field:hover {
  background-color: var(--color-grey--2);
}

.bookmarks:hover,
.page-options__btn--bookmarks:hover + .bookmarks {
  visibility: visible;
  opacity: 1;
}

.bookmarks .preview__link:link,
.bookmarks .preview__link:visited {
  border: none;
}

.bookmarks .preview__link:link:hover,
.bookmarks .preview__link:visited:hover {
  background-color: var(--color-grey--2);
}

.slider {
  width: 8.5rem;
  margin: 0 1rem;
  height: 4.2rem;
  border-radius: 2rem;
  background-color: var(--color-grey--2);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.2rem;
  position: relative;
}

.slider > *:not(:last-child) {
  margin-right: 1rem;
}

.slider svg {
  width: 2.5rem;
  height: 2.5rem;
  fill: var(--color-text--3);
}

.slider__circle {
  display: block;
  content: "";
  position: absolute;
  left: 1rem;
  width: 2.8rem;
  height: 2.8rem;
  border-radius: 50%;
  z-index: 100;
  background-image: var(--gradient);
  transition: all 0.2s;
}

@media only screen and (max-width: 28.12em) {
  .slider {
    background-color: var(--color-grey--1);
    position: absolute;
    z-index: 100;
    top: 11rem;
    left: 2rem;
  }
}

.navigation {
  position: relative;
  margin-right: 3rem;
  margin-left: 1rem;
  min-width: 8rem;
  height: 100%;
}

@media only screen and (max-width: 41.25em) {
  .navigation {
    margin-right: 2rem;
  }
}

.navigation__checkbox {
  display: none;
}

.navigation__button {
  display: block;
  background-image: var(--gradient);
  height: 5.3rem;
  width: 8rem;
  border-radius: 10rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2000;
  transition: all 0.4s;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
  cursor: pointer;
}

.navigation__background {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  background-image: var(--wrapper-gradient);
  opacity: 0.8;
  height: 5rem;
  width: 5rem;
  border-radius: 50%;
  transition: all 0.8s cubic-bezier(0.86, 0, 0.07, 1);
  z-index: 1000;
}

.navigation__nav {
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1500;
  visibility: hidden;
  opacity: 0;
  width: 0;
  transition: all 0.8s cubic-bezier(0.48, -0.36, 0.42, 1.39);
}

.navigation__list {
  list-style: none;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}

.navigation__item {
  margin: 0.5rem;
}

.navigation__link:link,
.navigation__link:visited {
  display: inline-block;
  font-size: 3.2rem;
  font-weight: 600;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  padding: 1.3rem 5rem;
  transition: all 0.6s;
  background-image: linear-gradient(
    120deg,
    transparent 0%,
    transparent 50%,
    var(--color-grey--3) 50%
  );
  background-size: 220%;
  -webkit-clip-path: polygon(10% 0, 100% 0, 90% 100%, 0 100%);
  clip-path: polygon(10% 0, 100% 0, 90% 100%, 0 100%);
}

.navigation__link:hover,
.navigation__link:active {
  background-position: 100%;
  color: #000;
}

.navigation__icon {
  position: relative;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.navigation__icon,
.navigation__icon::before,
.navigation__icon::after {
  display: inline-block;
  background-color: var(--color-grey--1);
  border-radius: 1rem;
  height: 3px;
  width: 3rem;
  transition: all 0.3s;
}

.navigation__icon::after,
.navigation__icon::before {
  content: "";
  position: absolute;
  left: 0;
}

.navigation__icon::before {
  top: -1rem;
}

.navigation__icon::after {
  top: 1rem;
}

.navigation__icon:hover .navigation__icon::before {
  top: -1.2rem;
}

.navigation__icon:hover .navigation__icon::after {
  top: 1.2rem;
}

.navigation__checkbox:checked + .navigation__button .navigation__icon {
  background-color: transparent;
}

.navigation__checkbox:checked + .navigation__button .navigation__icon::before {
  transform: rotate(135deg);
  background-color: var(--color-primary);
  top: 0;
}

.navigation__checkbox:checked + .navigation__button .navigation__icon::after {
  transform: rotate(-135deg);
  background-color: var(--color-primary);
  top: 0;
}

.navigation__checkbox:checked ~ .navigation__background {
  transform: translate(-50%, -50%) scale(150);
}

.navigation__checkbox:checked ~ .navigation__button {
  background-image: linear-gradient(var(--color-grey--1), var(--color-grey--2));
}

.navigation__checkbox:checked ~ .navigation__nav {
