    grid-template-columns: 20rem 1fr;
    grid-template-rows: repeat(4, 10rem);
  }
}

@media only screen and (max-width: 28.12em) {
  .planner {
    grid-template-columns: 15rem 1fr;
    grid-template-rows: repeat(4, 10rem);
  }
}

.planner__date {
  align-self: flex-start;
}

.planner__date-label {
  font-size: 1.7rem;
  margin-top: 3rem;
  text-transform: uppercase;
  padding: 1rem 5rem 1rem 2rem;
  background-color: var(--color-primary-variant);
  color: var(--color-on-primary);
  font-weight: 700;
  -webkit-clip-path: polygon(0 0, 100% 0, 85% 50%, 100% 100%, 0 100%);
  clip-path: polygon(0 0, 100% 0, 85% 50%, 100% 100%, 0 100%);
  transition: all 0.2s;
}

@media only screen and (max-width: 41.25em) {
  .planner__date-label {
    margin-top: 0;
    margin-bottom: 2rem;
  }
}

.planner__date-label:hover {
  padding-left: 3rem;
}

.planner__empty {
  grid-column: 1;
  grid-row: 1;
}

@media only screen and (max-width: 41.25em) {
  .planner__empty {
    display: none;
  }
}

.planner__day-cell {
  grid-column: 1;
  justify-self: left;
  align-self: center;
}

@media only screen and (max-width: 41.25em) {
  .planner__day-cell {
    display: none;
  }
}

.planner__meal-cell {
  grid-row: 1;
  place-self: center;
}

@media only screen and (max-width: 41.25em) {
  .planner__meal-cell {
    grid-column: 1;
    grid-row: unset;
  }
}

.planner__day {
  background-image: var(--gradient);
  color: var(--color-on-primary);
  min-width: 15rem;
  padding: 1rem 2rem;
  border-radius: 1rem;
}

@media only screen and (max-width: 75em) {
  .planner__day {
    min-width: 10rem;
    padding: 1rem;
    font-size: 1.4rem;
  }
}

.planner__meal {
  background-image: var(--gradient);
  color: var(--color-on-primary);
  min-width: 15rem;
  padding: 1rem 2rem;
  border-radius: 1rem;
}

@media only screen and (max-width: 75em) {
  .planner__meal {
    min-width: 12rem;
    font-size: 1.4rem;
  }
}

@media only screen and (max-width: 56.25em) {
  .planner__meal {
    min-width: 10rem;
    padding: 1rem;
    font-size: 1.4rem;
  }
}

.planner__grid {
  grid-column: 2 / span 4;
  grid-row: 2 / span 7;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(7, 10rem);
  border: 0.25px solid var(--color-grey--2);
}

@media only screen and (max-width: 41.25em) {
  .planner__grid {
    grid-column: 2 / span 1;
    grid-row: 1 / span 4;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 10rem);
    overflow: hidden;
  }
}

.planner__recipe {
  border: 0.25px solid var(--color-grey--2);
}

@media only screen and (max-width: 41.25em) {
  .planner__recipe {
    display: none;
  }
  .planner__recipe--active {
    display: initial;
  }
}

.planner__pagination {
  padding: 0;
}

@media only screen and (max-width: 41.25em) {
  .planner__pagination {
    margin: 1.5rem 2.5rem;
  }
}

.planner__form {
  display: none;
}

@media only screen and (max-width: 41.25em) {
  .planner__form {
    display: initial;
    align-self: center;
    margin-bottom: 4rem;
    margin-left: 12rem;
  }
}

@media only screen and (max-width: 41.25em) {
  .planner__select-label {
    display: none;
  }
  .planner__select {
    font-size: 1.8rem;
    padding: 0.8rem 0.5rem;
    max-width: 20rem;
    border-radius: 1rem;
    transition: all 0.2s;
    text-transform: uppercase;
    text-align: center;
    font-weight: 600;
    background-image: var(--gradient);
    color: var(--color-on-primary);
    outline: none;
