  }
  .planner__select:focus {
    outline: none;
    border: 1px solid var(--color-primary);
    background-color: var(--color-text--1);
  }
  .planner__option {
    color: var(--color-text--1);
    background-color: var(--color-grey--1);
    padding: 1rem 1.5rem;
    font-weight: 600;
  }
  .planner__submit {
    display: none;
  }
}

.planned {
  position: relative;
}

.planned__link:link,
.planned__link:visited {
  height: 100%;
  display: flex;
  place-content: center;
  padding: 1.5rem 3.25rem;
  transition: all 0.3s;
  text-decoration: none;
  overflow: hidden;
}

.planned__link:hover {
  background-color: var(--color-grey--2);
  transform: scale(1.05);
  box-shadow: 0.2rem 1.2rem 2rem rgba(0, 0, 0, 0.15);
}

.planned__link--active {
  background-color: var(--color-grey--2);
}

.planned__fig {
  flex: 0 0 5rem;
  align-self: center;
  border-radius: 50%;
  overflow: hidden;
  height: 5rem;
  margin-right: 2rem;
  position: relative;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.planned__fig::before {
  content: "";
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: linear-gradient(
    to right bottom,
    var(--color-grad-1),
    var(--color-grad-2)
  );
  opacity: 0.2;
}

.planned__fig img {
  display: block;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  transition: all 0.3s;
}

@media only screen and (max-width: 75em) {
  .planned__fig {
    display: none;
  }
}

@media only screen and (max-width: 41.25em) {
  .planned__fig {
    display: initial;
  }
}

@media only screen and (max-width: 21.875em) {
  .planned__fig {
    display: none;
  }
}

.planned__title {
  font-size: 1.25rem;
  color: var(--color-text--2);
  text-transform: uppercase;
  font-weight: 700;
  align-self: center;
  max-height: 7.5rem;
  overflow: hidden;
}

.planned__remove-btn {
  position: absolute;
  bottom: 0.75rem;
  right: 0.75rem;
  filter: brightness(0.95);
  height: 2rem;
  width: 2rem;
  border-radius: 10rem;
  transform: scale(1.05);
  transition: all 0.2s;
}

.planned__remove-btn svg {
  height: 1.2rem;
  width: 1.2rem;
  fill: var(--color-primary);
  transform: translate(0.5px, -3.5px);
}

.planned__remove-btn:hover {
  background-color: var(--color-primary);
  transform: scale(1.3);
}

.planned__remove-btn:hover svg {
  fill: var(--color-grey--2);
}

.features {
  display: flex;
  flex-flow: row wrap;
  place-content: center;
  padding: 0 8rem;
  margin-top: -1.5rem;
}

.features__feature-box {
  max-width: 25rem;
  background-color: var(--color-grey--2);
  box-shadow: 0.8rem 1.5rem 2rem rgba(0, 0, 0, 0.1);
  border-radius: 1rem;
  margin: 3rem 3.5rem;
  padding: 3rem 2.5rem;
  transition: all 0.3s;
}

.features__feature-box:hover {
  transform: translateY(-1rem) scale(1.03);
}

.features__icon {
  text-align: center;
}

.features__icon svg {
  width: 5rem;
  height: 5rem;
  fill: var(--color-primary);
}

.features__description {
  font-size: 1.4rem;
  font-weight: 600;
  color: var(--color-text--2);
}

